<script>
import Header from '../../../components/school-training/Header.vue'
import html2Canvas from 'html2canvas'
import JsPDF from 'jspdf' // 引入echarts
export default {
  name: 'StageReport',
  components: {
    Header
  },
  mounted () {
    this.initEvent()
    this.initData()
  },
  data () {
    return {
      winWidth: window.innerWidth,
      cpResult: [{
        content: '编程语言',
        result: '3% ⬆'
      }, {
        content: '测试技术',
        result: '3% ⬆'
      }, {
        content: '计算机网络基础',
        result: '3% ⬆'
      }, {
        content: '面向对象技术',
        result: '3% ⬆'
      }, {
        content: '软件工程技术',
        result: '3% ⬆'
      }],
      taskList: [{
        content: '编程语言',
        result: '3% ⬆'
      }, {
        content: '测试技术',
        result: '3% ⬆'
      }, {
        content: '计算机网络基础',
        result: '3% ⬆'
      }, {
        content: '面向对象技术',
        result: '3% ⬆'
      }, {
        content: '软件工程技术',
        result: '3% ⬆'
      }]
    }
  },
  methods: {
    initEvent: function () {
      let me = this
      me.winWidth = $('body').width()
      window.onresize = function () {
        me.winWidth = $('body').width()
      }
      me.$nextTick(function () {
        me.document = document.getElementById('myChart')
        me.document3 = document.getElementById('myChart3')
        me.drawLine()// => 'updated'
      })
    },
    initData: function () {
      let me = this
      this.$http.post('/doReport/summarizeReportInfo', {
        // 'teamId': localStorage.getItem('teamId')
        'teamId': '271'
      }, res => {
        console.log(res)
        if (res.success) {

        }
      })
    },
    drawLine () {
      // 雷达图
      var myChart = this.$echarts.init(this.document)
      // 绘制图表
      myChart.setOption({
        title: {
          // text: '能力对比'
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          x: 'center',
          y: '90%',
          data: ['第一次', '第二次']
        },
        radar: [
          {
            indicator: [
              { text: 'Java', max: 100 },
              { text: '前端', max: 100 },
              { text: '测试', max: 100 },
              { text: '部署', max: 100 },
              { text: '软件工程技术', max: 100 }
            ],
            center: ['50%', '45%'],
            radius: 80
          }
        ],
        series: [
          {
            type: 'radar',
            tooltip: {
              trigger: 'item'
            },
            itemStyle: {
              normal: {
                areaStyle: {
                  type: 'default'
                }
              }
            },
            data: [
              {
                value: [60, 73, 85, 40, 90],
                name: '第一次'
              },
              {
                value: [70, 63, 65, 60, 100],
                name: '第二次'
              }
            ]
          }
        ]
      })

      // 基于准备好的dom，初始化echarts实例
      var myChart3 = this.$echarts.init(this.document3)
      // 绘制图表
      myChart3.setOption({
        title: {
          // text: '动态数据',
          // subtext: '纯属虚构'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#283b56'
            }
          }
        },
        legend: {
          data: ['第一次排名', '第二次排名']
        },
        // toolbox: {
        //   show: true,
        //   feature: {
        //     dataView: { readOnly: false },
        //     restore: {},
        //     saveAsImage: {}
        //   }
        // },
        dataZoom: {
          show: false,
          start: 0,
          end: 100
        },
        xAxis: [
          {
            type: 'category',
            boundaryGap: true,
            data: ['java', '前端', '测试', '部署']
          },
          {
            type: 'category',
            boundaryGap: true,
            data: ['java', '前端', '测试', '部署']
          }
        ],
        yAxis: [
          {
            type: 'value',
            scale: true,
            name: '第一次排名',
            max: 100,
            min: 0,
            boundaryGap: [0.2, 0.2]
          },
          {
            type: 'value',
            scale: true,
            name: '第二次排名',
            max: 100,
            min: 0,
            boundaryGap: [0.2, 0.2]
          }
        ],
        series: [
          {
            name: '第一次排名',
            type: 'bar',
            // xAxisIndex: 1,
            // yAxisIndex: 1,
            data: (function () {
              var res = []
              var len = 4
              while (len--) {
                res.push(Math.round(Math.random() * 100))
              }
              return res
            })()
          },
          {
            name: '第二次排名',
            type: 'bar',
            data: (function () {
              var res = []
              var len = 0
              while (len < 4) {
                res.push((Math.random() * 10 + 5).toFixed(1) - 0)
                len++
              }
              return res
            })()
          }
        ]
      })
    },
    downloadReport () {
      // var title = this.title;
      var title = '总结报告'
      let PDF = new JsPDF('', 'pt', 'a4')
      // html2Canvas(document.querySelector('#pdfDom'), {
      // console.log(document.querySelector('.page-content'))
      var pdfHtml = document.querySelectorAll('.page-content')
      for (let i = 0; i < pdfHtml.length; i++) {
        $(function () {
          console.log(pdfHtml[i])
          html2Canvas(pdfHtml[i], {
            allowTaint: true
          }).then(function (canvas) {
            console.log(canvas)
            let contentWidth = canvas.width
            let contentHeight = canvas.height
            let imgWidth = 595.28
            let imgHeight = 592.28 / contentWidth * contentHeight
            let pageData = canvas.toDataURL('image/jpeg', 1.0)
            PDF.addImage(pageData, 'JPEG', 0, 0, imgWidth, imgHeight)
            PDF.addPage()
            if (i === pdfHtml.length - 1) {
              PDF.save(title + '.pdf')
            }
          })
        }, 500)
      }
    }
  }
}
</script>

